import { _decorator, Component, Node, PhysicsSystem2D, EPhysics2DDrawFlags, Collider2D, Contact2DType, IPhysics2DContact, BoxCollider2D, director, Sprite, Color, resources, SpriteFrame, Layers, find } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('BulletPerManeger')
export class BulletPerManeger extends Component {
    @property 
    isOverScreen:boolean = false;

    @property
    istarget:boolean = false;

    @property
    speed:number;

    @property
    bulletType:number;
    //1:散射 2：追击 3：光子 4：凤翼 11：为敌机

    @property
    angle:number;
    onLoad() {
        //绘制物理信息
        PhysicsSystem2D.instance.enable = true;
        PhysicsSystem2D.instance.debugDrawFlags = EPhysics2DDrawFlags.Aabb | 
            EPhysics2DDrawFlags.Pair | 
            EPhysics2DDrawFlags.CenterOfMass |
            EPhysics2DDrawFlags.Joint |
            EPhysics2DDrawFlags.Shape ;
    }

    start(){
        //注册单个碰撞体的回调函数
        console.log('tool information')
        let collider = this.node.getComponent(Collider2D);
        // console.log(collider);
        // console.log(collider);
        if (collider) {
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
            collider.on(Contact2DType.PRE_SOLVE, this.onPreSolve, this);
            collider.on(Contact2DType.POST_SOLVE, this.onPostSolve, this);
        }
    }

    update(deltaTime: number) {
        let x = this.node.position.x
        let y = this.node.position.y
        
        this.bulletAttack(deltaTime);

        if(x>800 || x<-800 || y>450 || y<-450){
            this.isOverScreen = true;
            console.log('this bullet over screen')
        }

    }

    onBeginContact (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 只在两个碰撞体开始接触时被调用一次
        // console.log('Bullet BOOMBOOM');
        // console.log("i am bullet , my target is",otherCollider)
        this.istarget = true;
        // this.node.destroy()
    }
    onEndContact (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 只在两个碰撞体结束接触时被调用一次
        // console.log('Bullet ENDEND');
        // this.node.destroy()

    }
    onPreSolve (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 每次将要处理碰撞体接触逻辑时被调用
        // console.log('Bullet and Emeny Work onPreSolve');
    }
    onPostSolve (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 每次处理完碰撞体接触逻辑时被调用
        console.log('Bullet and Emeny Work onPostSolve');

    }

    ifTarget():boolean{
        return this.istarget;
    }

    bulletAttack(deltaTime){
        if(this.bulletType == 1){
            this.PaoBullet(deltaTime);
        }

        if(this.bulletType == 11){
            this.EmenyBullet(deltaTime);
        }
    }

    PaoBullet(deltaTime){
        let x = this.node.position.x
        let y = this.node.position.y
        console.log("子弹的角度是",this.angle)
        x = x + this.speed * Math.sin(this.angle) * deltaTime;
        y = y + this.speed * Math.cos(this.angle) * deltaTime;

        this.node.setPosition(x,y,0)
    }

    EmenyBullet(deltaTime){
        let x = this.node.position.x
        let y = this.node.position.y

        x = x + this.speed * deltaTime;
        y = y - this.speed * deltaTime;

        this.node.setPosition(x,y,0)
    }

    // DanBullet(deltaTime){

    // }


}


